<template>
  <nav class="flex text-white justify-between">
        <div>
            <a href="/" class="font-extrabold text-3xl font-base">Thebuild</a>
        </div>
        <div class="flex mt-2 text-stone-400 gap-6">
            <a class="hover:text-white transition-all duration-300" href="/">Home</a>
            <a class="hover:text-white transition-all duration-300" href="/plans">House Plans</a>
            <a class="hover:text-white transition-all duration-300" href="/contact">Contact</a>
            <a v-if="user != null" class="hover:text-white transition-all duration-300" href="/contact">My Purchases</a>
            <a v-if="user != null" class="hover:text-white transition-all duration-300" href="/contact">My Wishlists</a>
        </div>
        <div>
            <a v-if="user == null" href="/login" class="border border-[#ededed] flex justify-center items-center rounded-full px-6 py-1 hover:bg-[#ededed] hover:text-[#101014] transition-colors duration-300">
                Log in</a>
                <button v-else @click="logout" class="border border-[#ededed] flex justify-center items-center rounded-full px-6 py-1 hover:bg-[#ededed] hover:text-[#101014] transition-colors duration-300">
                Log out</button>

        </div>
    </nav>
</template>

<script>
export default {
    name: 'Navbar',
    data() {
        return {
            user: null,
        }
    },
    methods: {
        logout() {
            localStorage.removeItem("the_build")
            this.$router.push({
                    name: 'home'
                })
        }
    },
    mounted() {
        this.user = JSON.parse(localStorage.getItem("the_build"))?.user
    }
}
</script>

<style>
html {
    background-color: #101014;
}
</style>
